.fridge-dialog {
  background: var(--background-body, #fff);
  border: none;
  border-radius: 16px;
  width: 90%;
  max-width: 420px;
  padding: 0;
  box-shadow: 0 10px 40px #0003;
}

.fridge-dialog::backdrop {
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  background: #0009;
}

.fridge-modal-container {
  padding: 1.5rem;
  position: relative;
}

.fridge-modal-header {
  text-align: center;
  margin-bottom: 1.5rem;
}

