  border: none;
  border-radius: 8px;
  margin-top: .5rem;
  padding: .875rem 1.5rem;
  font-size: 1rem;
  font-weight: 500;
  transition: background .2s, transform .1s;
}

.primary-btn:hover:not(:disabled) {
  background: var(--button-hover, #0056b3);
}

.primary-btn:active:not(:disabled) {
  transform: scale(.98);
}

.primary-btn:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.close-btn {
  cursor: pointer;
  color: var(--text-muted, #888);
  background: none;
  border: none;
  border-radius: 4px;
  padding: .25rem;
  font-size: 1.5rem;
  line-height: 1;
  transition: background .2s;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.close-btn:hover {
  background: var(--background-alt, #f0f0f0);
}

.fridge-indicator {
  cursor: pointer;
  border-radius: 20px;
